import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'app-geofences-list',
  templateUrl: './geofences-list.component.html',
  styleUrls: ['./geofences-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class GeofencesListComponent {
  @Input() geofencesList: any[] = [];
  displayedList: { id: number; name: string }[] = [];
  filterQuery: any = '';
  @Output() deleteGeofence = new EventEmitter<number>();
  @Output() editGeofence = new EventEmitter<number>();

  constructor() {}

  onDeleteGeofence(id: number) {
    this.deleteGeofence.emit(id);
  }

  onEditGeofence(id: number) {
    this.editGeofence.emit(id);
  }

  filterGeofences(event: any) {
    this.filterQuery = event.query;
    this.displayedList = this.geofencesList.filter(
      (e) => e.name.includes(this.filterQuery) || e.id == this.filterQuery
    );
  }
}
