  overlapHandler(polygon: google.maps.Polygon) {
    this.drawingState = 'Error';
    this.drawingManager.setDrawingMode(null);
    this.alertService.showAlert('warning', 'Polygon Overlaps with another polygon');

    polygon.setOptions({
      strokeColor: 'white',
      fillOpacity: 0.9,
      fillColor: 'red',
      draggable: true,
      editable: true,
    });

    console.log(polygon);

    polygon.addListener('dragend', (polygon1) => {
      let overlap = false;
      for (let i = 0; i < this.polygonsList.length; i++) {
        const overlapped = this.checkIntersection(this.polygonsList[i].polygon, polygon);
        const overlapping = this.checkIntersection(polygon, this.polygonsList[i].polygon);

        if (overlapped || overlapping) {
          overlap = true;
          break;
        } else {
          overlap = false;
        }
      }
      if (!overlap) {
        polygon.setOptions({
          strokeColor: this.ngColor,
          fillOpacity: 0.5,
          fillColor: this.ngColor,
          draggable: false,
          editable: false,
        });
        this.drawingState = 'completed';
        this.polygonsToAdd.push({
          id: this.polygonsList.length + 1,
          name: this.ngZoneName,
          color: this.ngColor,
          polygon: polygon,
        });

        this.submitForm({} as any);
      }
    });

    this.polygonsToRemove.push({
      polygon: polygon,
    });
  }

