  createDrawing() {
    this.polygonsToAdd = [];

    this.drawingManager.setOptions({
      drawingControl: true,
      polygonOptions: {
        draggable: false,
        fillColor: this.ngColor,
        strokeColor: this.ngColor,
        editable: false,
      },
    });

    this.drawingManager.setDrawingMode(OverlayType.POLYGON);

    this.drawingState = 'drawing';

    this.drawingManager.setMap(this.mapLoader.map);
  }

  checkIntersection(polygon: any, addedPolygon: any) {
    // eslint-disable-next-line no-debugger
    const polygonPath = addedPolygon.getPath();
    const polygonCoordinates = this.googleMaps2JTS(polygonPath);
    let isOverlapping = false;
    for (let i = 0; i < polygonCoordinates.length; i++) {
      const latlng = new google.maps.LatLng(polygonCoordinates[i].lat, polygonCoordinates[i].lng);
      const isWithinPolygon = polygon.containsLatLng(latlng);
      const isOverlapped = google.maps.geometry.poly.containsLocation(latlng, polygon);
      if (isWithinPolygon || isOverlapped) {
        isOverlapping = true;
        return isOverlapping;
      } else {
        isOverlapping = false;
      }
    }
    return isOverlapping;
  }

  googleMaps2JTS(boundaries: any) {
    const coordinates = [];

    for (let i = 0; i < boundaries.getLength(); i++) {
      const xy = boundaries.getAt(i);
      coordinates.push({
        lat: xy.lat(),
        lng: xy.lng(),
      });
    }
    return coordinates;
  }

  createPolygon() {
    // Define the LatLng coordinates for the polygon's path.
    const triangleCoords = [
      { lat: 25.774, lng: -80.19 },
      { lat: 18.466, lng: -66.118 },
      { lat: 32.321, lng: -64.757 },
      { lat: 25.774, lng: -80.19 },
    ];
    // Construct the polygon.
    const bermudaTriangle = new google.maps.Polygon({
      paths: triangleCoords,
      strokeColor: 'blue',
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: 'blue',
      fillOpacity: 0.35,
    });

    bermudaTriangle.setMap(this.mapLoader.map);

    this.drawingState = 'completed';
    this.polygonsToAdd.push({
      id: this.polygonsList.length + 1,
      name: this.ngZoneName,
      color: this.ngColor,
      polygon: bermudaTriangle,
    });
    this.submitForm({} as any);
  }
