export enum OverlayType {
  /**
   * Specifies that the DrawingManager creates circles, and that the overlay
   * given in the overlaycomplete event is a circle.
   */
  CIRCLE = 'circle',
  /**
   * Specifies that the DrawingManager creates markers, and that the overlay
   * given in the overlaycomplete event is a marker.
   */
  MARKER = 'marker',
  /**
   * Specifies that the DrawingManager creates polygons, and that the
   * overlay given in the overlaycomplete event is a polygon.
   */
  POLYGON = 'polygon',
  /**
   * Specifies that the DrawingManager creates polylines, and that the
   * overlay given in the overlaycomplete event is a polyline.
   */
  POLYLINE = 'polyline',
  /**
   * Specifies that the DrawingManager creates rectangles, and that the
   * overlay given in the overlaycomplete event is a rectangle.
   */
  RECTANGLE = 'rectangle',
}

export const createPolygon = (paths: any[]): google.maps.Polygon => {
  const strokeColor = 'rgb(47,67,67)';
  const strokeOpacity = 0.6;
  const fillOpacity = 0;
  const strokeWeight = 1;
  const fillColor = '';
  return new google.maps.Polygon({
    paths,
    strokeColor,
    strokeOpacity,
    fillOpacity,
    strokeWeight,
    fillColor,
  });
};

export function getHexagonsLocation(wktReader: { read: (arg0: any) => void; toJson: () => any }, wktLocations: any) {
  wktReader.read(wktLocations);
  const WKT_LOCATIONS = wktReader.toJson();
  // eslint-disable-next-line no-underscore-dangle
  const coordinates = WKT_LOCATIONS.coordinates[0];
  const lstHexagons = [];
  // eslint-disable-next-line no-debugger
  debugger;
  // eslint-disable-next-line @typescript-eslint/prefer-for-of
  // tslint:disable-next-line: prefer-for-of
  for (let index = 0; index < coordinates.length; index++) {
    lstHexagons.push({
      lat: coordinates[index][0],
      lng: coordinates[index][1],
    });
  }
  return lstHexagons;
}

export function polygonToWktObject(arrayPolygons) {
  var arrayPolygonWTKObjectsTest = [];
  for (var i = 0, len = arrayPolygons.length; i < len; i++) arrayPolygonWTKObjectsTest.push(arrayPolygons[i].ToWKT());
  return arrayPolygonWTKObjectsTest;
}
